"use client";

import { useMemo, useState } from "react";
import { Header } from "../components/layout/Header";
import { CategoryTabs } from "../components/desktop/CategoryTabs";
import { BottomNavbar } from "../components/mobile/BottomNavbar";
import { FilterSheet } from "../components/mobile/FilterSheet";
import { ListingStoryCard } from "../components/mobile/ListingStoryCard";
import { UserProfile } from "../components/mobile/UserProfile";
import { LISTINGS } from "../constants";
import type { Listing } from "../types";

type MainCategoryId = "imoveis" | "logistica" | "eventos";
type MobileTab = "home" | "filter" | "profile";

export default function HomePage() {
  const [mainCategory, setMainCategory] = useState<MainCategoryId>("imoveis");
  const [searchText, setSearchText] = useState<string>("");
  const [wishlist, setWishlist] = useState<string[]>([]);

  const [mobileTab, setMobileTab] = useState<MobileTab>("home");
  const [filterOpen, setFilterOpen] = useState(false);

  const [filterState, setFilterState] = useState<{
    tipo?: string;
    subTipo?: string;
    precoMin?: number;
    precoMax?: number;
    localizacao?: string;
  }>({});

  const filteredListings: Listing[] = useMemo(() => {
    let list = LISTINGS;

    list = list.filter((l) => {
      if (mainCategory === "imoveis") return l.domain === "imoveis";
      if (mainCategory === "logistica") return l.domain === "logistica";
      return l.domain === "eventos";
    });

    if (searchText.trim()) {
      const q = searchText.toLowerCase();
      list = list.filter(
        (l) =>
          l.title.toLowerCase().includes(q) ||
          l.location.toLowerCase().includes(q) ||
          l.description.toLowerCase().includes(q)
      );
    }

    if (filterState.localizacao) {
      const loc = filterState.localizacao.toLowerCase();
      list = list.filter((l) =>
        l.location.toLowerCase().includes(loc)
      );
    }
    if (typeof filterState.precoMin === "number") {
      list = list.filter((l) => l.price >= filterState.precoMin!);
    }
    if (typeof filterState.precoMax === "number") {
      list = list.filter((l) => l.price <= filterState.precoMax!);
    }

    return list;
  }, [mainCategory, searchText, filterState]);

  const [mobileIndex, setMobileIndex] = useState(0);
  const currentMobileListing = filteredListings[mobileIndex];

  const toggleWishlist = (id: string) => {
    setWishlist((prev) =>
      prev.includes(id) ? prev.filter((x) => x !== id) : [...prev, id]
    );
  };

  const handleApplyFilters = () => {
    if (mobileTab === "filter") {
      setMobileTab("home");
    }
  };

  const contentDesktop = (
    <main className="hidden md:block">
      <CategoryTabs
        value={mainCategory}
        onChange={setMainCategory}
        onOpenFilter={() => setFilterOpen(true)}
      />
      <section className="mx-auto max-w-6xl px-4 pb-10 pt-4 md:px-6 lg:px-8">
        {filteredListings.length === 0 ? (
          <div className="flex min-h-[40vh] flex-col items-center justify-center text-center">
            <p className="text-lg font-semibold">
              Nenhum anúncio encontrado para estes filtros.
            </p>
            <p className="mt-1 text-sm text-neutral-500">
              Ajuste os filtros ou a pesquisa para ver mais opções.
            </p>
          </div>
        ) : (
          <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 lg:grid-cols-3">
            {filteredListings.map((listing) => (
              <article
                key={listing.id}
                className="flex flex-col overflow-hidden rounded-2xl bg-white shadow-sm transition hover:shadow-md"
              >
                <img
                  src={listing.images[0]}
                  alt={listing.title}
                  className="h-52 w-full object-cover"
                />
                <div className="space-y-1 px-3 pb-3 pt-2">
                  <h2 className="line-clamp-1 text-sm font-semibold text-neutral-900">
                    {listing.title}
                  </h2>
                  <p className="text-xs font-medium uppercase tracking-wide text-neutral-500">
                    {listing.location}
                  </p>
                  <p className="text-sm font-semibold text-neutral-900">
                    {new Intl.NumberFormat("pt-MZ", {
                      style: "currency",
                      currency: "MZN",
                      maximumFractionDigits: 0,
                    }).format(listing.price)}
                    <span className="text-xs font-normal text-neutral-500">
                      {" "}
                      / dia
                    </span>
                  </p>
                </div>
              </article>
            ))}
          </div>
        )}
      </section>
    </main>
  );

  const contentMobile = (
    <main className="block md:hidden">
      <section className="mx-auto max-w-md px-4 pb-4 pt-2">
        {mobileTab === "home" && currentMobileListing && (
          <ListingStoryCard
            data={currentMobileListing}
            isWishlisted={wishlist.includes(currentMobileListing.id)}
            onToggleWishlist={() => toggleWishlist(currentMobileListing.id)}
          />
        )}
        {mobileTab === "profile" && <UserProfile />}
      </section>
    </main>
  );

  return (
    <div className="flex min-h-screen flex-col">
      <Header onSearch={setSearchText} />
      {contentDesktop}
      {contentMobile}

      <FilterSheet
        open={filterOpen}
        onOpenChange={(open) => {
          setFilterOpen(open);
        }}
        category={mainCategory}
        value={filterState}
        onChange={setFilterState}
        onApply={handleApplyFilters}
      />

      <BottomNavbar
        active={mobileTab}
        onChange={(tab) => {
          if (tab === "filter") {
            setFilterOpen(true);
          }
          setMobileTab(tab);
        }}
        hidden={filterOpen}
      />
    </div>
  );
}

