"use client";

import * as Icons from "lucide-react";
import { cn } from "../../lib/utils";

export type MainCategoryId = "imoveis" | "logistica" | "eventos";

const DESKTOP_CATEGORIES: {
  id: MainCategoryId;
  label: string;
  icon: keyof typeof Icons;
}[] = [
  { id: "imoveis", label: "Imóveis", icon: "Home" },
  { id: "logistica", label: "Logística", icon: "Truck" },
  { id: "eventos", label: "Eventos", icon: "CalendarDays" },
];

interface CategoryTabsProps {
  value: MainCategoryId;
  onChange: (value: MainCategoryId) => void;
  onOpenFilter: () => void;
}

export function CategoryTabs({
  value,
  onChange,
  onOpenFilter,
}: CategoryTabsProps) {
  return (
    <div className="sticky top-[4.25rem] z-30 border-b bg-white/80 backdrop-blur">
      <div className="mx-auto flex max-w-6xl items-center gap-3 px-4 py-2 md:px-6 lg:px-8">
        <div className="no-scrollbar flex flex-1 gap-4 overflow-x-auto">
          {DESKTOP_CATEGORIES.map((cat) => {
            const Icon = (Icons as any)[cat.icon] as React.ComponentType<{
              className?: string;
            }>;
            const active = value === cat.id;
            return (
              <button
                key={cat.id}
                type="button"
                onClick={() => onChange(cat.id)}
                className={cn(
                  "flex min-w-[90px] flex-col items-center gap-1 border-b-2 px-1 pb-2 text-xs font-medium text-neutral-500 transition",
                  active
                    ? "border-neutral-900 text-neutral-900"
                    : "border-transparent hover:text-neutral-800"
                )}
              >
                <Icon
                  className={cn(
                    "h-5 w-5",
                    active ? "text-neutral-900" : "text-neutral-500"
                  )}
                />
                <span>{cat.label}</span>
              </button>
            );
          })}
        </div>

        <button
          type="button"
          onClick={onOpenFilter}
          className="hidden items-center gap-2 rounded-full border border-neutral-300 px-4 py-2 text-xs font-semibold text-neutral-800 shadow-sm hover:border-neutral-900 md:inline-flex"
        >
          <Icons.SlidersHorizontal className="h-4 w-4" />
          Filtros
        </button>
      </div>
    </div>
  );
}

