"use client";

import { Home, SlidersHorizontal, User2 } from "lucide-react";
import { cn } from "../../lib/utils";

type TabId = "home" | "filter" | "profile";

interface BottomNavbarProps {
  active: TabId;
  onChange: (tab: TabId) => void;
  hidden?: boolean;
}

export function BottomNavbar({ active, onChange, hidden }: BottomNavbarProps) {
  if (hidden) return null;

  return (
    <nav className="fixed inset-x-0 bottom-0 z-40 border-t border-neutral-200 bg-white/95 px-6 pb-5 pt-2 shadow-[0_-6px_18px_rgba(15,23,42,0.05)] md:hidden">
      <div className="mx-auto flex max-w-md items-center justify-between gap-8 text-xs font-medium">
        <NavItem
          icon={Home}
          label="Home"
          active={active === "home"}
          onClick={() => onChange("home")}
        />
        <NavItem
          icon={SlidersHorizontal}
          label="Filtro"
          active={active === "filter"}
          onClick={() => onChange("filter")}
        />
        <NavItem
          icon={User2}
          label="Perfil"
          active={active === "profile"}
          onClick={() => onChange("profile")}
        />
      </div>
    </nav>
  );
}

interface NavItemProps {
  icon: React.ComponentType<{ className?: string }>;
  label: string;
  active: boolean;
  onClick: () => void;
}

function NavItem({ icon: Icon, label, active, onClick }: NavItemProps) {
  return (
    <button
      type="button"
      onClick={onClick}
      className={cn(
        "flex flex-1 flex-col items-center gap-1 rounded-full px-3 py-1.5 transition",
        active ? "text-neutral-900" : "text-neutral-500"
      )}
    >
      <span
        className={cn(
          "flex h-9 w-9 items-center justify-center rounded-full border text-sm",
          active
            ? "border-neutral-900 bg-neutral-900 text-white shadow-sm"
            : "border-transparent bg-neutral-100"
        )}
      >
        <Icon className="h-4 w-4" />
      </span>
      <span>{label}</span>
    </button>
  );
}

